@import "../../../App/styles/variables";

.buttons {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap-reverse;
  row-gap: 10px;

  margin-top: auto;

  &__btnToCart {
    background: $base-color;
    mix-blend-mode: normal;

    width: 120px;
    height: 60px;

    margin-top: auto;
    margin-right: 15px;

    border: none;

    transition: all $transition-long ease;

    &:hover {
      background: $base-color;

      transition: all $transition-long ease;

      box-shadow: 0px 5px 10px rgba(253, 114, 92, 0.22),
        0px 20px 40px rgba(250, 99, 35, 0.24);
    }

    &_color {
      background: $btn-active;

      mix-blend-mode: normal;

      color: $white-color;

      border: none;

      transition: all $transition-long ease;

      &:hover {
        box-shadow: 0px 5px 10px rgba(253, 114, 92, 0.22),
          0px 20px 40px rgba(250, 99, 35, 0.24);

        transition: all $transition-long ease;
      }
    }
  }
}